.BOX{
    display: flex;
}
.BOX span{
    margin-left: 15px;
}
.delete{
    padding: 8px;
    background-color: red;
    color: white;
    cursor: pointer;
}
.edit{
    padding: 8px;
    background-color: rgb(88, 187, 218);
    color: white;
    cursor: pointer;
}
.editRemove{
    color: black;
    cursor: pointer;
    position: relative;
    margin-left: 10px;
    margin-right: 10px;
}
.editRemove:hover{
    color: green;
}
.addPlus{
    display: flex;
    align-items: center;
    margin-top: 10px;
}
.color{
    display: flex;
    justify-content: center;
    align-items: center;
}
.inputSpec{
    margin-top: 10px;
}
.addFile{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-flow: column;
}
.inputFile{
    display: flex;
    flex-flow: column;
}
input[type="file"]{
    display: none;
}
.addImage{
    background-color: rgb(47, 178, 187);
    padding:5px 10px;
    border-radius: 10px;
    color: white;
    font-size: 15px;
}
.inputFile{
    padding: 50px;
    border: 4px dashed #ddd;
    margin-top: 10px;
    font-size: 20px;
}
.centercontain{
    display: flex;
    align-items: center;
}
.imageBrand{
    width: 200px;
    margin: 7px;
}
.td{
    display: flex;
    flex-flow: column;
    justify-content: space-between;
}
.tabel thead tr th {
    width: 7%;
}
.tabel img{
    width: 45px;
    height: 45px;
    border-radius: 25px;
}
.tabel tr{
    height: 15px;
}
.iconBox i{
    color: white;
}
.iconBox{
    display: flex;
    justify-content: space-between;
}
.Box{
    width: 80px;
    display: flex;
}
.Boxred{
    color: red;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    cursor: pointer;
}
.Boxred:hover{
    background-color: red;
    color: white;
}
.BoxValue{
    padding: 25px;
}
.BoxMainMedia{
    width: 90%;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;

}
.BoxMedia{
    width: 20%;
    box-shadow: 0 2px 3px #ddd;
    padding: 10px;
    position: relative;
}
.BoxMedia img{
    width: 100%;
    height: 100%;
}
.removeBtnImage{
    position: absolute;
    left: 11px;
    top: 6px;
    cursor: pointer;
}
.removeBtnImage:hover{
    color: red;
}